import React, { Component } from 'react';
import './App.css';
var uuid = require('uuid');
var firebase = require('firebase');

var config = {
    apiKey: "AIzaSyCSsDziIuOkWMkIjQySRPAX8OsLIhEBYdM",
    authDomain: "simpletriviasurv.firebaseapp.com",
    databaseURL: "https://simpletriviasurv.firebaseio.com",
    projectId: "simpletriviasurv",
    storageBucket: "simpletriviasurv.appspot.com",
    messagingSenderId: "87088779063"
  };
  firebase.initializeApp(config);

class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      id:uuid.v1(),
      name: '',
      answers: {
        q1: '',
        q2: '',
        q3: '',
      },
      submitted: false
    }
    this.handleQuestionChange = this.handleQuestionChange.bind(this);
  }

  handleNameSubmit(event){
    var name = this.refs.name.value;
    this.setState({name:name}, function(){
      console.log(this.state);
    });
    event.preventDefault();
  }
  handleQuestionSubmit(event){
      firebase.database().ref('surveys/'+this.state.id).set({
        name: this.state.name,
        answers:this.state.answers
      });
      this.setState({submitted:true}, function(){
        console.log('Questions submitted..');
      });
      event.preventDefault();
  }
  handleQuestionChange(event){
    var answers = this.state.answers;
    if(event.target.name === 'q1'){
      answers.q1 = event.target.value;
    }else if(event.target.name === 'q2'){
      answers.q2 = event.target.value;
    }else if(event.target.name === 'q3'){
      answers.q3 = event.target.value;
    }
    this.setState({answers:answers}, function(){
      console.log(this.state);
    });
  }

  render() {
    var user;
    var questions;
    if(this.state.name && this.state.submitted === false){
      user = <h2 className="royal-blue">Welcome {this.state.name}</h2>
      questions = <span>
        <h3 className="royal-blue">Survey Questions</h3>
        <form onSubmit={this.handleQuestionSubmit.bind(this)}>
        <div>
          <label>1. What breaks when you say it?</label><br />
          <input type="radio" name="q1" value="Glass" onChange={this.handleQuestionChange}/> Glass<br />
          <input type="radio" name="q1" value="Mirror"onChange={this.handleQuestionChange}/> Mirror<br />
          <input type="radio" name="q1" value="Silence" onChange={this.handleQuestionChange}/> Silence<br />
          <input type="radio" name="q1" value="It"onChange={this.handleQuestionChange}/> It<br />
        </div>
        <div>
          <label>2. What is 1,000,000 divided by 0?</label><br />
          <input type="radio" name="q2" value="1" onChange={this.handleQuestionChange}/> 1<br />
          <input type="radio" name="q2" value="0" onChange={this.handleQuestionChange}/> 0<br />
          <input type="radio" name="q2" value="Infinity" onChange={this.handleQuestionChange}/> Infinity<br />
          <input type="radio" name="q2" value="1000000" onChange={this.handleQuestionChange}/> 1,000,000<br />
        </div>
        <div>
          <label>3. Adam is smaller than John, Jamie is taller than Alex, John is taller than Jamie, Jamie is smaller than Adam. Who is the tallest?</label><br />
          <input type="radio" name="q3" value="Jamie" onChange={this.handleQuestionChange}/> Jamie<br />
          <input type="radio" name="q3" value="Alex" onChange={this.handleQuestionChange}/> Alex<br />
          <input type="radio" name="q3" value="Adam" onChange={this.handleQuestionChange}/> Adam<br />
          <input type="radio" name="q3" value="John" onChange={this.handleQuestionChange}/> John<br />
        </div>
        <input type="submit" value="Submit" />
        </form>

      </span>
    } else if (!this.state.name && this.state.submitted === false){
      user = <span>
        <h2 className="royal-blue">Please enter your name to begin the survey.</h2>
        <form onSubmit={this.handleNameSubmit.bind(this)}>
          <input type="text" placeholder="Alan Fresco..." ref="name" />
        </form>
        </span>
        questions = '';
    } else if (this.state.submitted === true){
      user = <h2 className="royal-blue">{this.state.name}, You are a superstar! <br />Thank you for submitting the survey.</h2>
    }
    return (
      <div className="App">
        <div className="App-header">
          <h2 className="text-center">React Trivia Survey | Server Firebase</h2>
        </div>
        <div className="text-center">
          {user}
        </div>
        <div className="container">
          {questions}
        </div>
      </div>
    );
  }
}

export default App;



